
import { defineComponent } from 'vue'
import { useRoute } from 'vue-router'
import { testData, articles } from '@/testData'
export default defineComponent({
  setup () {
    const route = useRoute()
    const info = testData.find(i => i.id === (+route.params.id))
    const articleLists = articles.filter(i => i.columnId === (+route.params.id))
    return {
      route,
      info,
      articleLists
    }
  }
})
